function formatDate (val) {
  // 
  let start = new Date(val)
  let y = start.getFullYear()
  let m = (start.getMonth() + 1) > 10 ? (start.getMonth() + 1) : '0' + (start.getMonth() + 1)
  let d = start.getDate() > 10 ? start.getDate() : '0' + start.getDate()
  return y + '-' + m + '-' + d
}

function mistiming (sDate1, sDate2) {
  // 
  let aDate, oDate1, oDate2, iDays
  aDate = sDate1.split('-')
  oDate1 = new Date(aDate[1] + '-' + aDate[2] + '-' + aDate[0])
  aDate = sDate2.split('-')
  oDate2 = new Date(aDate[1] + '-' + aDate[2] + '-' + aDate[0])
  iDays = parseInt(Math.abs(oDate1 - oDate2) / 1000 / 60 / 60 / 24)
  return iDays + 1
}

function countDate (start, end) {
  // 90
  let days = mistiming(start, end)
  let stateT = days > 90 ? Boolean(0) : Boolean(1)
  return {
    state: stateT,
    day: days
  }
}
function timeForMat (count) {
  // 
  let time1 = new Date()
  time1.setTime(time1.getTime() - (24 * 60 * 60 * 1000))
  let Y1 = time1.getFullYear()
  let M1 = ((time1.getMonth() + 1) > 10 ? (time1.getMonth() + 1) : '0' + (time1.getMonth() + 1))
  let D1 = (time1.getDate() > 9 ? time1.getDate() : '0' + time1.getDate())
  let timer1 = Y1 + '-' + M1 + '-' + D1 // 
  let time2 = new Date()
  time2.setTime(time2.getTime() - (24 * 60 * 60 * 1000 * count))
  let Y2 = time2.getFullYear()
  let M2 = ((time2.getMonth() + 1) > 10 ? (time2.getMonth() + 1) : '0' + (time2.getMonth() + 1))
  let D2 = (time2.getDate() > 9 ? time2.getDate() : '0' + time2.getDate())
  let timer2 = Y2 + '-' + M2 + '-' + D2 // 730
  return {
    t1: timer2,
    t2: timer1
  }
}

function yesterday (start, end) {
  // 
  let timer = timeForMat(1)
  return timer
}

function timeSlot (days) {
  // 3
  if (days === 90) {
    const time = new Date()
    const t1 = time.getFullYear() + '-' + (time.getMonth() - 1) + '-01'
    const t2 = time.getFullYear() + '-' + (time.getMonth() + 1) + '-' + time.getDate()
    return {
      t1: t1,
      t2: t2
    }
  }
  // 7
  let timer = timeForMat(days)
  return timer
}

export {
  formatDate,
  countDate,
  yesterday,
  timeSlot
}



